.duration{background:#fff;position:relative;z-index:100}.duration .logo img{-webkit-animation-duration:4s;animation-duration:4s;left:20px;max-width:450px;position:absolute;top:20px;width:100%;z-index:2}@media (max-width:992px){.duration .logo img{left:auto}}#img{position:relative}#img img{-o-object-fit:cover;height:100vh;object-fit:cover;width:100%}#img .discription{align-items:center;bottom:130px;display:flex;flex-wrap:wrap;justify-content:flex-start;padding:0 60px;position:absolute;text-align:left}@media (max-width:992px){#img .discription{padding:0 30px}}#img .discription .link{color:#fff;cursor:pointer;font-size:25px;font-weight:300;line-height:30px;margin-bottom:20px;text-align:left;text-decoration:none}#img .discription .textWhite{color:hsla(0,0%,100%,.6);font-size:14px;font-weight:300;height:1px;line-height:24px;margin:0;opacity:0;overflow:hidden;transition:all .5s ease 0s}#img:before{background:rgba(0,0,0,.75);content:"";height:100%;opacity:0;position:absolute;transition:all .5s ease 0s;width:100%}#img .separator{background:#fff;height:1px;margin-top:35px;opacity:.5;transition:all .5s ease 0s;width:20%}#img:hover:before{opacity:1!important}#img:hover .separator{width:70%}#img:hover .textWhite{height:auto;opacity:1;overflow:auto}.downMenu{-webkit-animation:down 1s ease-in both;animation:down 1s ease-in both;opacity:0}@-webkit-keyframes down{0%{-webkit-transform:translate3d(0,-30%,0);opacity:0;transform:translate3d(0,-30%,0)}to{-webkit-transform:translateZ(0);opacity:1;transform:translateZ(0)}}@keyframes down{0%{-webkit-transform:translate3d(0,-30%,0);opacity:0;transform:translate3d(0,-30%,0)}to{-webkit-transform:translateZ(0);opacity:1;transform:translateZ(0)}}.downFade{opacity:0;overflow:hidden;pointer-events:none!important;transition:all .3s}


